			care1_8: "A kind-hearted person that wants to build a happy world",
			care1_9: "Culture",
			care1_10:
				"A horizontal communication culture freely expressing opinions and respecting each other",
			care1_10_1:
				"An organization that works and achieves results as a team, not individual",
			care1_10_2:
				"Operating quarterly workshops to improve teamwork through group activities",
			care1_10_3:
				"A culture in which all executives and employees share projects promoted by the company",
			care1_10_4: "A delightful office space with exciting music",
			care1_11: "Benefits",
			care1_12: "Support for Life Events",
			care1_13:
				"Complemented with congratulations or condolences, we offer PTO, expenses, and supplies.",
			care1_14: "Anniversary Gifts",
			care1_15: "We offer gifts with gratitude on special days.",
			care1_16: "Support for Family Love",
			care1_17:
				"We support PTO and expenses for those who want to become parents",
			care1_18: "Support for Cultural Life",
			care1_19:
				"We support group viewing for movies, musicals, sports games, etc. for a fun cultural life.",
			care1_20: "Beverage & Snack Bar",
			care1_21:
				"Snacks and drinks are provided to keep members from being famished.",
			care1_22: "Equipment Support",
			care1_23: "We provide optimized equipment and software for each role.",
			care1_24: "Clubs",
			care1_25:
				"We run an in-house club to promote members` hobbies and mutual friendship.",
			care1_26: "Group Injury Insurance",
			care1_27:
				"In order to reduce the burden of medical expenses for members, we provide coverage insurance premiums and medical expenses.",
			care1_28: "Medical Checkup",
			care1_29:
				"For the health of members, we conduct free examinations once a year and provide vacations.",
			care1_30: "Mental Health",
			care1_31: "We support professional care to those in need of help.",
			news1: "Galaxy News",
			news2: "Press Release",
			news3:
				'Galaxy Corporation Awarded "Minister of SMEs Award for Regulatory Innovation"',
			news4:
				'"AVA Dream" Delivers Dreams and Hopes. Three Special Points to Watch Out For."',
			news5: "Press",
			news11: "Galaxy News",
			news12: "Press Release",
			news13:
				'Galaxy Corporation Awarded "Minister of SMEs Award for Regulatory Innovation"',
			news14: "Publication Date: December 20, 2022 Daily Safety Newspaper",
			news15: "Source: Daily Safety Newspaper (https://idsn.co.kr )",
			news16:
				'Galaxy Corporation has been recognized for contributing to the Avatar craze originating from South Korea.<br /><br />On the 19th of last month, Galaxy Corporation announced that it had received the "Ministers Award" at the "2022 South Korea Small and Medium Business Regulatory Innovation Awards" held at the Korea Federation of Small and Medium Business in Yeouido, Seoul.<br /><br />This award was established to encourage individuals who have made significant contributions to regulatory innovation this year. A total of 75 people were honored, including 54 outstanding civil servants and officials from public organizations and associations who have actively pursued regulatory innovation and efficient administration, as well as 21 small and medium-sized business owners who have excelled in social contribution and regional development.<br /><br />Galaxy Corporation had the honor of receiving the Ministers Award (Minister Lee Young) alongside Korea South-East Power and Kangwon Land.<br /><br />Galaxy Corporation is not only a pioneering company in the metaverse field as a Metaverse First Mover, but it also possesses over 200 domestic and international artist avatar (AVA) intellectual properties, utilizing them as metaverse avatars. It is the first domestic metaverse avatar portal platform company.<br /><br />In the future, Galaxy Corporation plans to establish its unique metaverse universe, similar to Disney Marvel, and unveil an A-to-Z portal strategy for metaverse, encompassing broadcasting, music, commerce, concerts, and more, based on metaverse avatars.<br /><br />Recently, it gained attention by presenting the TV Chosun metaverse AI music show "Avadream." In particular, the tribute stage where avatars of late stars like Kim Sung-jae of Deux, the late actress Kim Ja-ok, and the late singer Seo Ji-won appeared evoked special emotions. Alongside the release of the movie "Avatar 2," it was acclaimed for contributing to the metaverse and avatar craze in the second half of 2022.<br /><br />Furthermore, Galaxy Corporation is actively engaged in various aspects of the metaverse industry, including the establishment and operation of top-level metaverse courses at KAIST and Yonsei University, collaboration with the Korea Aerospace Research Institute on the "Nuriho and Danuri Launch," metaverse live streaming, and the establishment of a metaverse media outlet called "Metarism." <br /><br />Choi Yong-ho, Chief Happiness Officer of Galaxy Corporation, expressed his gratitude for receiving the Ministers Award, stating, "It is an honor to receive this award," and added, "We will strive to achieve our goal of listing on the KOSDAQ next year, working tirelessly to ensure the global metaverse industry grows together."',
