import React, { useState } from "react";
import { Document, Page } from "react-pdf";

import styled from "styled-components";

import Pdf from "../assets/pdf/testpdf.pdf";
import Test from "../assets/pdf/test.jpg";

const PopupStyle = styled.div`
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 9999;
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	justify-content: center;
	align-items: center;
`;
const PopupContentStyle = styled.div`
	background-color: darkgray;
	padding: 10px;
	border-radius: 8px;
`;

const Popup = () => {
	const [showPopup, setShowPopup] = useState(true);

	const handleClose = () => {
		setShowPopup(false);
	};

	const handleNeverShowAgain = () => {
		//          .
		//  ,        .
		//      .
		setShowPopup(false);
	};

	if (!showPopup) {
		return null; //    null   .
	}

	return (
		<PopupStyle>
			<PopupContentStyle>
				<Document file={Pdf}>
					<Page pageNumber={1} />
				</Document>

				{/* iframe    */}
				{/* <iframe src={Pdf} width="100%" height="100%"></iframe> */}
				{/* <source src={Pdf} type="application/pdf" /> */}

				{/* <img src={Test} alt="slash" /> */}

				<button onClick={handleClose}></button>
				<button onClick={handleNeverShowAgain}>   </button>
			</PopupContentStyle>
		</PopupStyle>
	);
};

export default Popup;
